import {Avatar, List, ListItem, ListItemAvatar, ListItemText, styled} from "@mui/material";
import {
    Link as RouterLink, useLocation,
} from "react-router-dom";

const DivvyAvatar = styled(Avatar)({
    backgroundColor: 'transparent'
})

export default function ProjectList(props) {

    const {projects} = props;

    const { pathname } = useLocation();

    return (
        <List>
            {
                projects.map((project, i) => {
                    return <RouterLink
                        key={project.symbol}
                        to={`/${project.symbol}`}
                        style={{textDecoration: 'none', color: 'inherit'}}
                    >
                        <ListItem
                            button
                            style={{backgroundColor: pathname === `/${project.symbol}` ? '#eee' : ''}}
                        >
                            <ListItemAvatar>
                                <DivvyAvatar>
                                    <img
                                        src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract}/logo.png`}
                                        width={40}/>
                                </DivvyAvatar>
                            </ListItemAvatar>
                            <ListItemText primary={project.name} secondary={project.symbol}/>
                        </ListItem>
                    </RouterLink>
                })
            }
        </List>
    )
}