import Web3 from 'web3';
import {useEffect, useState} from "react";
import {ArcSeries, FlexibleWidthXYPlot} from "react-vis";

import {
    Alert,
    Avatar,
    Grid,
    IconButton, Link,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText, Paper,
    Tooltip,
    Typography
} from "@mui/material";

import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import LocalFireDepartmentIcon from '@mui/icons-material/LocalFireDepartment';
import CachedIcon from '@mui/icons-material/Cached';
import LocalOfferIcon from '@mui/icons-material/LocalOffer';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import ShieldIcon from '@mui/icons-material/Shield';
import CalculateIcon from '@mui/icons-material/Calculate';
import WebAssetIcon from '@mui/icons-material/WebAsset';
import TwitterIcon from '@mui/icons-material/Twitter';
import TelegramIcon from '@mui/icons-material/Telegram';
import ShowChartIcon from '@mui/icons-material/ShowChart';

import abi from '../abis/abi.json';
import sd_abi from '../abis/sd_abi.json';
import axios from "axios";
import {useSelector} from "react-redux";

const web3 = new Web3('https://bsc-dataseed.binance.org/');

// const divvyContract = '0xe9490A983292bD844074Ac3e83c3cb408f578b61';
const fBNBContract = '0x87b1AccE6a1958E522233A737313C086551a5c76';
const deadWallet = '0x000000000000000000000000000000000000dead';
const divvyWallet = '0xb80267eA7fa368374ee4d4Bf10044778232AdEFe';

const fBNBcontract = new web3.eth.Contract(abi, fBNBContract)

const NumberFormatter = new Intl.NumberFormat('en-US', {
    maximumSignificantDigits: 5
});

export default function ProjectStats(props) {

    const {bnbPrice} = useSelector(state => state.divvy);

    const {project} = props;

    const [burnt, setBurnt] = useState(0);
    const [liquidity, setLiquidity] = useState(0);
    const [fbnb, setFbnb] = useState(0);
    const [supply, setSupply] = useState(0);
    const [backing, setBacking] = useState(0);
    const [backingPerToken, setBackingPerToken] = useState(0);
    const [percent, setPercent] = useState(0);
    const [angle, setAngle] = useState(0);
    const [divvyBalance, setDivvyBalance] = useState(0);
    const [infoJSON, setInfoJSON] = useState({});
    const [loading, setLoading] = useState(true);

    const contract = new web3.eth.Contract(sd_abi, project.contract);

    const listingMin = 0.01;//percentage

    const resetValues = () => {
        setBurnt(0);
        setLiquidity(0);
        setFbnb(0);
        setSupply(0);
        setBacking(0);
        setBackingPerToken(0);
        setPercent(0);
        setAngle(0);
        setDivvyBalance(0)
        setInfoJSON({});
        setLoading(true)
    }

    useEffect(() => {
        resetValues();

        axios
            .get(`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract}/info.json`)
            .then(resp => {
                setInfoJSON(resp.data);
                setLoading(false)
            })

        //balance of fBNB in Divvy
        fBNBcontract.methods.balanceOf(project.contract).call((err, resp) => {
            if (!err) {
                setFbnb(resp / Math.pow(10, 18))
            }
        })

        //balance of divvy in Liquidity
        contract.methods.getBalance(project.contract).call((err, resp) => {
            if (!err) {
                setLiquidity(resp / Math.pow(10, 9))
            }
        })

        // contract.methods.balanceOf(divvyContract).call((err, resp) => {
        //     if (!err) {
        //         setLiquidity(resp / Math.pow(10, 9))
        //     }
        // })

        //balance of divvy in dead wallet
        contract.methods.balanceOf(deadWallet).call((err, resp) => {
            if (!err) {
                setBurnt(resp / Math.pow(10, 9))
            }
        })

        //total supply
        contract.methods.totalSupply().call((err, resp) => {
            if (!err) {
                setSupply(resp / Math.pow(10, 9))
            }
        })

        //asset backing
        contract.methods._totalSupply5().call((err, resp) => {
            if (!err) {
                setBacking(resp / Math.pow(10, 18))
            }
        })

        //backing per token
        contract.methods.oneTokentoBacking().call((err, resp) => {
            if (!err) {
                setBackingPerToken(resp / Math.pow(10, 18))
            }
        })

        contract.methods._totalSupply1().call((err, resp) => {
            // console.log('_totalSupply1', resp / Math.pow(10, 9), err)
        })

        contract.methods._totalSupply2().call((err, resp) => {
            // console.log('_totalSupply2', resp / Math.pow(10, 9), err)
        })

        //balance of tokens in Divvy Fund
        contract.methods.balanceOf(divvyWallet).call((err, resp) => {
            if (!err) {
                setDivvyBalance(resp / Math.pow(10, 9))
            }
        })

    }, [project]);

    useEffect(() => {
        if (burnt && supply) {
            setPercent(burnt / supply);
            setAngle(360 * (burnt / supply) * (Math.PI / 180))
        }
    }, [burnt, supply]);

    if (loading) {
        return <></>
    }

    return (<Grid container spacing={2}>

        {
            (divvyBalance * ((fbnb - backing) / liquidity)) < 2 && <Grid item xs={12}>
                <Alert severity="warning">Listing on the Divvy Dashboard doesn't guarantee the security of a project, as
                    always Do Your Own Research #DYOR</Alert>
            </Grid>
        }

        <Grid item xs={6}>
            <Grid container direction="column">
                <Grid item alignSelf="center">
                    <img
                        src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract}/logo.png`}
                        alt={`${project.logo} Logo`}
                        style={{borderRadius: '50%', marginTop: 20, marginBottom: 20}}
                    />
                </Grid>
                <Grid item alignSelf="center">
                    <Grid container spacing={2} alignContent="center">

                        {
                            infoJSON.website && <Grid item>
                                <Tooltip title="Website">
                                    <IconButton
                                        href={infoJSON.website}
                                        target="_blank"
                                        style={{
                                            backgroundColor: project.primary,
                                            color: project.secondary
                                        }}
                                    >
                                        <WebAssetIcon/>
                                    </IconButton>
                                </Tooltip>
                            </Grid>
                        }

                        {
                            infoJSON.social && infoJSON.social.twitter && <Grid item>
                                <Tooltip title="Twitter">
                                    <IconButton
                                        href={infoJSON.social.twitter}
                                        target="_blank"
                                        style={{
                                            backgroundColor: project.primary,
                                            color: project.secondary
                                        }}
                                    >
                                        <TwitterIcon/>
                                    </IconButton>
                                </Tooltip>
                            </Grid>
                        }
                        {
                            infoJSON.social && infoJSON.social.telegram && <Grid item>
                                <Tooltip title="Telegram">
                                    <IconButton
                                        href={infoJSON.social.telegram}
                                        target="_blank"
                                        style={{
                                            backgroundColor: project.primary,
                                            color: project.secondary
                                        }}
                                    >
                                        <TelegramIcon/>
                                    </IconButton>
                                </Tooltip>
                            </Grid>
                        }
                        <Grid item>
                            <Tooltip title="Charts">
                                <IconButton
                                    href={`https://charts.fegex.com/charts/${project.contract}`}
                                    target="_blank"
                                    style={{
                                        backgroundColor: project.primary,
                                        color: project.secondary
                                    }}
                                >
                                    <ShowChartIcon/>
                                </IconButton>
                            </Tooltip>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>

        <Grid item xs={6}>
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Paper style={{padding: '10px 20px'}}>
                        <Typography color="primary"
                                    variant="h6">{NumberFormatter.format(divvyBalance) + ` ${project.symbol}`}</Typography>
                        <Typography variant="caption">Divvy Fund Balance</Typography>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: '10px 20px'}}>
                        <Typography color="primary"
                                    variant="h6">{NumberFormatter.format((divvyBalance * (fbnb - backing) / liquidity) || 0) + ` BNB`} -
                            ${NumberFormatter.format((divvyBalance * ((fbnb - backing) / liquidity) * bnbPrice) || 0) + ` USD`}</Typography>
                        <Typography variant="caption">Divvy Fund Value</Typography>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: '10px 20px'}}>
                        <Typography color="primary"
                                    variant="h6">{NumberFormatter.format(project.divvyFund === 0 ? (divvyBalance > (supply * listingMin) ? divvyBalance - (supply * listingMin) : 0) : divvyBalance - project.divvyFund) + ` ${project.symbol}`}</Typography>
                        <Typography variant="caption">Divvy Fund Reflections</Typography>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: '10px 20px'}}>
                        <Typography color="primary"
                                    variant="h6">{NumberFormatter.format(project.divvyFund === 0 ? (divvyBalance > (supply * listingMin) ? (divvyBalance - (supply * listingMin)) * ((fbnb - backing) / liquidity) : 0) : (divvyBalance - project.divvyFund) * ((fbnb - backing) / liquidity)) + ` ${project.symbol}` + ` BNB`}</Typography>
                        <Typography variant="caption">Divvy Fund Reflections Value</Typography>
                    </Paper>
                </Grid>
            </Grid>
        </Grid>

        {
            divvyBalance < (supply * listingMin) && !project.bypassMin ? <Grid item xs={12} style={{marginTop: 30}}>
                <Paper style={{paddingTop: 20}} align="center">
                    <Typography>Pending Listing. To be considered for listing on Divvy Dashboard
                        send {NumberFormatter.format(supply * listingMin)} {project.symbol} to the <Link
                            href="https://bscscan.com/address/0xb80267ea7fa368374ee4d4bf10044778232adefe"
                            target="_blank">Divvy Fund</Link></Typography>


                    <Grid item xs={12}>
                        <FlexibleWidthXYPlot
                            xDomain={[-5, 5]}
                            yDomain={[-5, 5]}
                            height={300}
                            style={{
                                transform: 'translate(-15px, 5px)',
                            }}
                        >
                            <ArcSeries
                                radiusDomain={[0, 10]}
                                center={{x: 0, y: 0}}
                                data={[
                                    {
                                        angle0: 0,
                                        angle: Math.PI * 2,
                                        opacity: 1,
                                        radius: 6.75,
                                        radius0: 6,
                                        color: '#eee',
                                    },
                                    {
                                        angle0: 0,
                                        angle: (360 * (divvyBalance / (supply * listingMin)) * (Math.PI / 180)),
                                        opacity: 1,
                                        radius: 8.5,
                                        radius0: 6,
                                        color: project.primary,
                                    },
                                ]}
                                colorType={'literal'}
                            />
                        </FlexibleWidthXYPlot>
                        <Typography
                            style={{
                                height: 0,
                                width: '100%',
                                textAlign: 'center',
                                top: -190,
                                position: 'relative',
                                fontSize: 30,
                                fontWeight: '700',
                                color: project.primary
                            }}
                        >
                            {NumberFormatter.format((supply * listingMin) - divvyBalance)}
                            <span
                                style={{
                                    fontSize: 12,
                                    fontWeight: '400',
                                    display: 'block',
                                }}
                            >UNTIL LISTED</span>
                        </Typography>
                    </Grid>
                </Paper>
            </Grid> : <>

                <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        {infoJSON.name && infoJSON.symbol &&
                            <Typography variant="h5">{infoJSON.name} ({infoJSON.symbol})</Typography>}
                        {infoJSON.id && <Link href={infoJSON.explorer}><Typography
                            variant="caption">{infoJSON.id}</Typography></Link>}
                        {infoJSON.description &&
                            <Typography variant="body1" style={{marginTop: 20}}>{infoJSON.description}</Typography>}
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <Paper>
                                <Grid container>
                                    <Grid item xs={6}>
                                        <FlexibleWidthXYPlot
                                            xDomain={[-5, 5]}
                                            yDomain={[-5, 5]}
                                            height={300}
                                            style={{
                                                transform: 'translate(-15px, 5px)',
                                            }}
                                            // className={classes.xyplot}
                                        >
                                            <ArcSeries
                                                radiusDomain={[0, 10]}
                                                center={{x: 0, y: 0}}
                                                data={[
                                                    {
                                                        angle0: 0,
                                                        angle: Math.PI * 2,
                                                        opacity: 1,
                                                        radius: 6.75,
                                                        radius0: 6,
                                                        color: '#eee',
                                                    },
                                                    {
                                                        angle0: 0,
                                                        angle: angle,
                                                        opacity: 1,
                                                        radius: 8.5,
                                                        radius0: 6,
                                                        color: project.primary,
                                                    },
                                                ]}
                                                colorType={'literal'}
                                            />
                                        </FlexibleWidthXYPlot>
                                        <Typography
                                            style={{
                                                height: 0,
                                                width: '100%',
                                                textAlign: 'center',
                                                top: -190,
                                                position: 'relative',
                                                fontSize: 30,
                                                fontWeight: '700',
                                                color: project.primary
                                            }}
                                        >
                                            {(percent * 100).toFixed(2)}
                                            <span
                                                style={{
                                                    fontSize: 12,
                                                    fontWeight: '400',
                                                    marginTop: -10,
                                                    display: 'block',
                                                }}
                                            >% BURNED</span>
                                        </Typography>
                                    </Grid>

                                    <Grid item xs={6} style={{marginTop: 20}}>
                                        <List>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar
                                                        sx={{bgcolor: project.primary}}><AccountBalanceIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(supply) + ` ${project.symbol}`}
                                                    secondary="Total Supply"/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar
                                                        sx={{bgcolor: project.primary}}><LocalFireDepartmentIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(burnt) + ` ${project.symbol}`}
                                                    secondary="Burnt Supply"/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><CachedIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(supply - burnt) + ` ${project.symbol}`}
                                                    secondary="Circulating Supply"/>
                                            </ListItem>
                                        </List>
                                    </Grid>
                                </Grid>
                            </Paper>
                        </Grid>
                        <Grid item xs={12}>
                            <Paper style={{padding: 20}}>
                                <Grid container>
                                    <Grid item xs={6}>
                                        <List>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><LocalOfferIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format((fbnb - backing) / liquidity) + " BNB"}
                                                    secondary={`Price - $${NumberFormatter.format((fbnb - backing) / liquidity * bnbPrice)}`}/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><ShieldIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(backingPerToken) + " fBNB"}
                                                    secondary="Backing Per Token"/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><CalculateIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={(((fbnb - backing) / liquidity) / backingPerToken).toFixed(2) + "x"}
                                                    secondary="Baseline To Market Factor"/>
                                            </ListItem>
                                        </List>
                                    </Grid>
                                    <Grid item xs={6}>
                                        <List>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><AttachMoneyIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(liquidity) + ` ${project.symbol}`}
                                                    secondary={`Liquidity ${project.symbol}`}/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><AttachMoneyIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText primary={NumberFormatter.format(fbnb - backing) + " fBNB"}
                                                              secondary="Liquidity fBNB"/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><ShieldIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText primary={NumberFormatter.format(backing) + " fBNB"}
                                                              secondary="Total Backing"/>
                                            </ListItem>
                                        </List>
                                    </Grid>
                                </Grid>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
            </>
        }
    </Grid>)
}