import axios from "axios";
import {useEffect} from "react";
import {
    AppBar,
    createTheme,
    CssBaseline,
    Grid, Hidden,
    ThemeProvider,
    Toolbar, Typography
} from "@mui/material";
import {
    HashRouter as Router,
    Routes,
    Route,
} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";

import ProjectList from "./components/ProjectList";
import ProjectStats from "./components/ProjectStats";
import {updateBNBPrice, updateProjects} from "./redux/slices/divvySlice";
import {useGetTickersByTypeQuery} from "./redux/services/BinanceTickerApi";

import Logo from './images/logo_small.png';

const theme = createTheme({
    palette: {
        primary: {
            main: '#872eff'
        }
    }
});

function DivvyDashboard() {

    const dispatch = useDispatch();
    const {bnbPrice, projects} = useSelector(state => state.divvy);

    // get BNB price
    let binance = useGetTickersByTypeQuery('price?symbol=BNBUSDT', {
        pollingInterval: 10000,
    });

    useEffect( () => {
        if (binance.status === 'fulfilled') {
            dispatch(updateBNBPrice({price: binance.data.price}));
        }
    }, [binance.status, binance.data, dispatch])

    //get projects list
    useEffect(() => {
        axios
            .get('https://raw.githubusercontent.com/DivvySD/dashboard/main/projects.json')
            .then(resp => {
                dispatch(updateProjects(resp.data));
            })
    }, [dispatch]);

    if (!projects.length || bnbPrice === 0) return <></>;

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline/>
            <AppBar color="primary">
                <Toolbar>
                    <img alt="Divvy" src={Logo} height={32}/>
                    <div style={{flexGrow: 1}}/>
                    <Typography>BNB ${bnbPrice.toFixed(2)}</Typography>
                </Toolbar>
            </AppBar>
            <Hidden mdUp>
                <Grid container style={{marginTop: 80}}>
                    <Grid item xs={12} style={{padding: 20}}>
                        <Typography>Divvy Dashboard is currently built for a desktop experience. We will be working on a
                            mobile version soon.</Typography>
                    </Grid>
                </Grid>
            </Hidden>
            <Hidden mdDown>
                <Router>
                    <Grid container style={{marginTop: 64}}>
                        <Grid item style={{
                            width: '25%',
                            height: 'calc(100% - 64px)',
                            overflow: 'auto',
                            position: 'fixed',
                            borderRight: '1px solid #eee'
                        }}>
                            <ProjectList projects={projects}/>
                        </Grid>

                        <Grid item xs={9} style={{
                            left: '25%',
                            height: 'calc(100% - 64px)',
                            overflow: 'auto',
                            padding: 40,
                            position: 'fixed',
                            backgroundColor: '#fafafa'
                        }}>
                            <Routes>
                                <Route
                                    path={`/`}
                                    exact={true}
                                    element={<ProjectStats project={projects[0]}/>}
                                />
                                {
                                    projects.map((project, i) => {
                                        return <Route
                                            key={i}
                                            path={`/${project.symbol}`}
                                            exact={true}
                                            element={<ProjectStats project={project}/>}
                                        />
                                    })
                                }
                            </Routes>
                        </Grid>
                    </Grid>
                </Router>
            </Hidden>
        </ThemeProvider>
    );
}

export default DivvyDashboard;